import React, { useCallback, useEffect, useState } from 'react';
import { Container, Row, Col } from 'react-bootstrap'
import SelectDropdown from './dropdowns/SelectDropdown';
import SearchBtn from './buttons/SearchBtn';
import { useForm, Controller, useFieldArray } from 'react-hook-form'

const FilterForm = ({ getSearchData }) => {

    const [formstate, setFormstate] = useState(null)


    const handleFormState = useCallback((label, data) => {
        setFormstate({
            ...formstate,
            [label]: data
        })
    }, [formstate])

    const handleSearch = () => {
        if (formstate) {
            getSearchData(formstate)
        }
    }


    return (
        <div className='filter__form'>
            <Container>
                <Row>
                    <Col lg={12} className='form__holder'>
                        {/* <form> */}
                            <SelectDropdown width={300} label="property type" handleFormState={handleFormState} />
                            <SelectDropdown width={240} label="bedroom" handleFormState={handleFormState} />
                            <SelectDropdown width={240} label="floor area" handleFormState={handleFormState} />
                            <SelectDropdown width={180} label="price" handleFormState={handleFormState} />
                            <SearchBtn onClick={handleSearch} />
                        {/* </form> */}
                    </Col>
                </Row>
            </Container>
        </div>
    );
};

export default FilterForm;