import React, { useState, memo, useCallback } from 'react';
import { Popover, Divider, Checkbox } from 'antd'
import { Form } from 'react-bootstrap'
import { DropDownIcon } from '../../../constants/svg/icons'
import ResetBtn from '../buttons/ResetBtn';
import ApplyBtn from '../buttons/ApplyBtn';
import { Controller } from 'react-hook-form'

const SelectContent = memo(({ handleApply, label, handleFormState }) => {

    return <div>
        <div className='select__content__body'>
            <Checkbox.Group
                onChange={(v) => handleFormState(label, v)}
            >
                <ul className='list'>
                    {
                        Array(9).fill('').map((el, idx) => (
                            <li key={idx}>
                                <Checkbox
                                    value={idx + 1}>{idx + 1}</Checkbox>
                            </li>
                        ))
                    }
                </ul>
            </Checkbox.Group>
        </div>
        <Divider />
        <div className='select__content__footer'>
            <ResetBtn onClick={() => console.log(";l")} />
            <ApplyBtn onClick={handleApply} />
        </div>
    </div>
})


const SelectDropdown = ({ width, label, handleFormState }) => {
    const [open, setOpen] = useState(false);
    const [customlabel, setCustomlabel] = useState(label.split(' ').join('_'));


    const handleOpenChange = (newOpen) => {
        setOpen(newOpen)
    }

    const handleApply = () => {
        setOpen(false)
    }


    return (
        <Popover
            content={<SelectContent handleApply={handleApply} label={customlabel} handleFormState={handleFormState} />}
            title={label}
            trigger="click"
            open={open}
            placement='bottomLeft'
            onOpenChange={handleOpenChange}
            arrow={false}
            overlayClassName='select__popovercontainer'
            overlayInnerStyle={{
                borderRadius: '0px',
                padding: '20px'
            }}
        >
            <div
                className='custom__select'
                style={{
                    width: width + 'px'
                }}
            >
                <div className='inner'>
                    {label}
                    <DropDownIcon />
                </div>
            </div>
        </Popover>
    );
};

export default SelectDropdown;