import React, { memo, useState } from 'react';
import { Container, Row, Col } from 'react-bootstrap'
import Card from './Card';
import {SortDropIcon} from '../../constants/svg/icons'


const ProductListing = ({ searchData }) => {
    const [data, setData] = useState([
        {
            bedroom: 3,
            property_type: 'unfurnished',
            floor__area: '10,000',
            price: '123455'
        },
        {
            bedroom: 3,
            property_type: 'unfurnished',
            floor__area: '10,000',
            price: '123455'
        },
        {
            bedroom: 2,
            property_type: 'unfurnished',
            floor__area: '10,000',
            price: '123455'
        },
        {
            bedroom: 3,
            property_type: 'unfurnished',
            floor__area: '10,000',
            price: '123455'
        },
        {
            bedroom: 3,
            property_type: 'unfurnished',
            floor__area: '10,000',
            price: '123455'
        },
        {
            bedroom: 4,
            property_type: 'unfurnished',
            floor__area: '10,000',
            price: '123455'
        },
        {
            bedroom: 2,
            property_type: 'unfurnished',
            floor__area: '10,000',
            price: '123455'
        },
        {
            bedroom: 2,
            property_type: 'unfurnished',
            floor__area: '10,000',
            price: '123455'
        },
        {
            bedroom: 5,
            property_type: 'unfurnished',
            floor__area: '10,000',
            price: '123455'
        },
    ])





    return (
        <div className='product__listing__container'>
            <Container>
                <Row>
                    <Col lg={12}>
                        <div className='sort__container'>
                            <div className='sort__btn'>
                                <p>Sort By :</p>
                                <button>Resect <SortDropIcon /></button>
                            </div>
                        </div>
                    </Col>
                </Row>
                <Row className='gy-5'>
                    {
                        data.map((el, idx) => (
                            <Col key={idx} sm={12} lg={4} xl={3}>
                                <Card
                                    data={el}
                                    isProduct={true} />
                            </Col>
                        ))
                    }
                </Row>
            </Container>
        </div>
    );
};

export default memo(ProductListing);